import React from "react";
import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <footer className="bg-gray-800 text-white py-8">
      <div className="container mx-auto px-4">
        <div className="flex flex-col md:flex-row justify-between items-center">
          <div className="mb-4 md:mb-0">
            <h2 className="text-lg font-bold">NIT Rourkela Club Portal</h2>
            <p className="text-sm">
              Discover and join the clubs that interest you the most.
            </p>
          </div>
          <div className="flex space-x-4 mb-4 md:mb-0">
            <Link
              to="/"
              className="hover:text-gray-400 transition-colors duration-200"
            >
              <i className="fab fa-facebook-f"></i>
            </Link>
            <Link
              to="/"
              className="hover:text-gray-400 transition-colors duration-200"
            >
              <i className="fab fa-twitter"></i>
            </Link>
            <Link
              to="/"
              className="hover:text-gray-400 transition-colors duration-200"
            >
              <i className="fab fa-instagram"></i>
            </Link>
            <Link
              to="/"
              className="hover:text-gray-400 transition-colors duration-200"
            >
              <i className="fab fa-linkedin-in"></i>
            </Link>
          </div>
          <div className="text-lg">
            <span className="  transition-all duration-200">
              Website made by
              <Link
                className=" mx-1 hover:font-semibold hover:text-orange-400"
                to="https://www.linkedin.com/in/sourav-mahapatra-443941253/"
              >
                Sourav Mahapatra
              </Link>
            </span>
          </div>
        </div>
        <div className="flex flex-col md:flex-row justify-between items-center mt-4">
          <div className="flex space-x-4">
            <Link
              to="/"
              className="hover:text-gray-400 transition-colors duration-200"
            >
              Home
            </Link>
            <Link
              to="/about"
              className="hover:text-gray-400 transition-colors duration-200"
            >
              About
            </Link>
            <Link
              to="/user/categories"
              className="hover:text-gray-400 transition-colors duration-200"
            >
              Clubs
            </Link>
            <Link
              to="/contact"
              className="hover:text-gray-400 transition-colors duration-200"
            >
              Contact
            </Link>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
