import React, { useState, useEffect } from "react";
import { Layout } from "../../components/Layout";
import axios from "axios";
import { toast } from "react-toastify";
import { useLocation } from "react-router-dom";
const Applications = () => {
  const [applications, setApplications] = useState([]);
  const [filteredApplications, setFilteredApplications] = useState([]);
  const [clubId, setClubId] = useState("");
  const [showPopup, setShowPopup] = useState(false);
  const [selectedApplication, setSelectedApplication] = useState(null);
  const [message, setMessage] = useState("");

  const getApplications = async () => {
    try {
      const res = await axios.get("/api/v1/admin/get-applications", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        setApplications(res.data.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getApplications();
  }, []);

  const handleAccept = (application) => {
    setSelectedApplication({ ...application, status: "accepted" });
    setShowPopup(true);
  };

  const handleReject = (application) => {
    setSelectedApplication({ ...application, status: "rejected" });
    setShowPopup(true);
  };

  const handlePopupSubmit = async () => {
    try {
      await axios.post(
        "/api/v1/admin/update-application",
        {
          applicationId: selectedApplication._id,
          status: selectedApplication.status,
          message: message,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      toast.success(`Application ${selectedApplication.status} successfully`);
      window.location.reload();
      setShowPopup(false);
      setMessage("");
      getApplications(); // Refresh the list
    } catch (error) {
      console.log(error);
      toast.error(`Failed to ${selectedApplication.status} the application`);
    }
  };

  const handlePopupClose = () => {
    setShowPopup(false);
    setMessage("");
  };

  const handleSearch = () => {
    const filtered = applications.filter((app) => app.clubId === clubId);
    setFilteredApplications(filtered);
  };

  return (
    <Layout>
      <div className="min-h-screen bg-gray-900 text-white p-6">
        <h1 className="text-3xl font-bold mb-6 text-center">Applications</h1>
        <div className="mb-6 text-center">
          <p className="text-lg mb-2">
            Enter the Club ID of the club whose applications you want to review:
          </p>
          <div className="flex justify-center">
            <input
              type="text"
              value={clubId}
              onChange={(e) => setClubId(e.target.value)}
              className="p-2 bg-gray-800 text-white rounded-l-lg"
              placeholder="Enter Club ID"
            />
            <button
              onClick={handleSearch}
              className="p-2 bg-green-500 text-white rounded-r-lg hover:bg-green-600 transition-colors duration-200"
            >
              Search
            </button>
          </div>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredApplications.map((application) => (
            <div
              key={application._id}
              className="bg-gray-800 p-4 rounded-lg shadow-lg hover:shadow-xl transition-shadow duration-300"
            >
              <p className="mb-2">
                <span className="font-semibold">Student Name:</span>{" "}
                {application.name}
              </p>
              <p className="mb-2">
                <span className="font-semibold">Student Roll No:</span>{" "}
                {application.identifier}
              </p>
              <p className="mb-2">
                <span className="font-semibold">Club ID:</span>{" "}
                {application.clubId}
              </p>
              <p className="mb-2">
                <span className="font-semibold">Status:</span>{" "}
                {application.status}
              </p>
              {application.status === "pending" ? (
                <div className="flex space-x-4 mt-4">
                  <button
                    onClick={() => handleAccept(application)}
                    className="flex-1 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors duration-200"
                  >
                    Accept
                  </button>
                  <button
                    onClick={() => handleReject(application)}
                    className="flex-1 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors duration-200"
                  >
                    Reject
                  </button>
                </div>
              ) : (
                <h1>Application has been reviewed</h1>
              )}
            </div>
          ))}
        </div>

        {showPopup && (
          <div className="fixed inset-0 bg-gray-900 bg-opacity-75 flex items-center justify-center z-50">
            <div className="bg-gray-800 p-6 rounded-lg shadow-lg max-w-md w-full">
              <h2 className="text-2xl font-bold mb-4">Send Message</h2>
              <textarea
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                className="w-full p-2 bg-gray-700 rounded-lg text-white mb-4"
                rows="4"
                placeholder="Enter your message here"
              ></textarea>
              <div className="flex space-x-4">
                <button
                  onClick={handlePopupSubmit}
                  className="flex-1 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors duration-200"
                >
                  Send
                </button>
                <button
                  onClick={handlePopupClose}
                  className="flex-1 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors duration-200"
                >
                  Cancel
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
};

export default Applications;
