import React, { useState } from "react";
import { useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

const Navbar = () => {
  const { user } = useSelector((state) => state.user);
  const [isOpen, setIsOpen] = useState(false);
  const navigate = useNavigate();

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  const handleLogout = () => {
    localStorage.clear();
    toast.success("Logout Successful");
    navigate("/login");
  };

  const handleiconclick = () => {
    navigate("/user/applications");
  };

  return (
    <nav className="bg-gray-800 text-white p-4 shadow-lg">
      <div className="container mx-auto flex items-center justify-between">
        <div className="flex items-center space-x-4">
          <i
            className="fas fa-bell text-xl cursor-pointer hover:text-gray-400"
            onClick={handleiconclick}
          ></i>
          <Link
            to="/profile"
            className="flex items-center space-x-2 cursor-pointer hover:text-gray-400"
          >
            <span>
              Welcome, <span className="font-semibold">{user?.name}</span>
            </span>
          </Link>
        </div>
        <div className="hidden md:flex items-center space-x-6">
          <Link
            to="/"
            className="hover:text-gray-400 transition-colors duration-200"
          >
            Home
          </Link>
          {user?.role === "student" ? (
            <>
              <Link
                to="/about"
                className="hover:text-gray-400 transition-colors duration-200"
              >
                About Us
              </Link>

              <Link
                to="/user/categories"
                className="hover:text-gray-400 transition-colors duration-200"
              >
                Clubs
              </Link>
              <Link
                to="/user/applications"
                className="hover:text-gray-400 transition-colors duration-200"
              >
                My Applications
              </Link>
            </>
          ) : (
            <>
              <Link
                to="/add-club"
                className="hover:text-gray-400 transition-colors duration-200"
              >
                Add Club
              </Link>
              <Link
                to="/admin/view-clubs"
                className="hover:text-gray-400 transition-colors duration-200"
              >
                View Clubs
              </Link>
              <Link
                to="/admin/applications"
                className="hover:text-gray-400 transition-colors duration-200"
              >
                Applications
              </Link>
            </>
          )}
          <button
            onClick={handleLogout}
            className="hover:text-gray-400 transition-colors duration-200"
          >
            Logout
          </button>
        </div>
        <div className="md:hidden flex items-center">
          <button
            onClick={toggleMenu}
            className="text-white focus:outline-none"
          >
            <i className="fas fa-bars text-2xl"></i>
          </button>
        </div>
      </div>
      {isOpen && (
        <div className="md:hidden mt-2 space-y-2">
          <Link to="/" className="block px-2 py-1 hover:bg-gray-700 rounded">
            Home
          </Link>
          {user?.role === "student" ? (
            <>
              <Link
                to="/about"
                className="block px-2 py-1 hover:bg-gray-700 rounded"
              >
                About Us
              </Link>
              <Link
                to="/user/categories"
                className="block px-2 py-1 hover:bg-gray-700 rounded"
              >
                Clubs
              </Link>
              <Link
                to="/user/applications"
                className="block px-2 py-1 hover:bg-gray-700 rounded"
              >
                My Applications
              </Link>
            </>
          ) : (
            <>
              <Link
                to="/add-club"
                className="block px-2 py-1 hover:bg-gray-700 rounded"
              >
                Add Club
              </Link>
              <Link
                to="/admin/view-clubs"
                className="block px-2 py-1 hover:bg-gray-700 rounded"
              >
                View Clubs
              </Link>
              <Link
                to="/admin/applications"
                className="block px-2 py-1 hover:bg-gray-700 rounded"
              >
                Applications
              </Link>
            </>
          )}
          <button
            onClick={handleLogout}
            className="block w-full text-left px-2 py-1 hover:bg-gray-700 rounded"
          >
            Logout
          </button>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
